<template>
  <div class="card">
    <div class="card-header">
      Exam04Inject
    </div>
    <div class="card-body">
      <div>
        <p>data1: {{data1}}</p>
        <p>data2.name1: {{data2.name1}}</p>
        <p>data2.name2: {{data2.name2}}</p>
        <p>data3.name1: {{data3.name1}}</p>
        <p>data3.name2: {{data3.name2}}</p>
        <button class="btn btn-info btn-sm" @click="changeData">데이터 변경</button>
      </div>
      <hr/>
      <ChildA class="mt-2"/>
      <ChildB class="mt-2"/>
    </div>
  </div>
</template>

<script setup>
import ChildA from "./ChildA";
import ChildB from "./ChildB";
import { ref, reactive, provide } from "vue";

const data1 = ref("parent-value1");
const data2 = ref({
  name1: "parent-value2",
  name2: "parent-value3"
});
const data3 = reactive({
  name1: "parent-value4",
  name2: "parent-value5"
});

provide("message", { data1, data2, data3 });

function changeData() {
  data1.value = "parent-data10";
  data2.value.name1 = "parent-data20";
  data2.value.name2 = "parent-data30";
  data3.name1 = "parent-data40";
  data3.name2 = "parent-data50";
}
</script>

