<template>
  <div class="card">
    <div class="card-header">
      Child
    </div>
    <div class="card-body">
      <div>
        <h6>[자식->부모]</h6>
        <button v-on:click="$emit('child-event-1', data1)" class="btn btn-danger btn-sm mr-2">child-event-1 emit</button>
        <button @click="handleBtn1" class="btn btn-danger btn-sm">child-event-2 emit</button>
      </div>
      <hr/>
      <div>
        <h6>[자식->부모->자식]</h6>
        <p class="mb-2">Counter: {{counter}}</p>
        <button v-on:click="handleBtn2" class="btn btn-info btn-sm mr-2">increment-event emit</button>
        <button @click="handleBtn3" class="btn btn-info btn-sm">decrement-event emit</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from "vue";

const props = defineProps(["counter"]);
const data1 = ref("value1");
const data2 = ref(100);
const emit = defineEmits(["child-event-1", "child-event-2", "increment-event", "decrement-event"]);

function handleBtn1() {
  emit("child-event-2", data1.value, data2.value);
}

function handleBtn2() {
  emit("increment-event", data2.value);
}

function handleBtn3() {
  emit("decrement-event", data2.value);
}
</script>

