<template>
  <div class="card">
    <div class="card-header">
      Exam01Props
    </div>
    <div class="card-body">
      <ChildA prop1="value1"
              prop2="value2" 
              productNo="1" 
              product-kind="bag"
              :productPrice="1000"/>
      <hr/>
      <ChildB  v-bind:productNo="product.no" 
                :productKind="product.kind" 
                v-bind="product"
                v-bind:product="product"/>
      <button class="btn btn-info btn-sm mt-2" @click="changeData">데이터 변경</button>
      <hr/>
      <ChildC
              propA="문자열"
              :propB="1" 
              :prop-c="2" 
              :prop-d="'valuec'"
              :prop-e="3"
              :prop-f="{message:'안녕하세요'}"
              :prop-g="['red', 'green', 'blue']"
              :prop-h="() => {return 'Prop Value'}"
              :prop-i="4"
              />
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import ChildA from './ChildA.vue';
import ChildB from './ChildB.vue';
import ChildC from './ChildC.vue';

const product = ref({
  no: 1,
  kind: "bag"
});

function changeData() {
  product.value.no += 1;
  product.value.kind += " bag";
}
</script>

